/*
*
* Box model reset
* http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/
*
*/
*,
*:before,
*:after {
  box-sizing: inherit;
}
html:before,
html:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  pointer-events: none;
}
html {
/* Stops devices from scaling up text. */
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  font-size: 100%;
  box-sizing: border-box;
  position: relative;
}
html:before {
  z-index: 100000;
  background-image: linear-gradient(rgba(233,0,0,0.8) 1px, rgba(0,0,0,0) 1px);
  background-size: 100% 1.5rem;
}
html:after {
  z-index: 99999;
  background-image: linear-gradient(rgba(0,233,0,0.6) 1px, rgba(0,0,0,0) 1px);
  background-size: 100% 0.375rem;
}
body {
  font: normal normal 16px/1.5rem -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  background-color: #fff;
  color: #000;
  margin: 0;
  padding: 0;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-decoration-skip-ink: auto;
  font-smoothing: auto;
  font-feature-settings: "liga", "kern";
}
/*
*
* Better Font Smoothing in Chrome & Safari
* http://www.intridea.com/blog/2014/5/8/better-font-smoothing-in-chrome-on-mac-os-x
*
*/
@media only screen and (-webkit-min-device-pixel-ratio: 1.25), only screen and (min-device-pixel-ratio: 1.25), only screen and (min-resolution: 200dpi), only screen and (min-resolution: 1.25dppx) {
  body {
    -webkit-font-smoothing: subpixel-antialiased;
  }
}
